@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(14, 18, 26);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App{max-width:100%;height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Montserrat",sans-serif}h1{font-family:"Montserrat",sans-serif;font-weight:700;font-size:35px;color:#e6e6e6;text-align:center;margin-bottom:23px}.todo{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}.todo li{list-style-type:none;color:#e6e6e6}.form-div{max-width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.form-div input{padding:6px 6px;border-radius:4px;border-bottom-right-radius:0;border-top-right-radius:0;font-family:"Montserrat",sans-serif;border:none;width:90%;outline:none;color:#e6e6e6;background-color:#40465c}.form-div button{padding:6px 6px;border-radius:4px;border-top-left-radius:0;border-bottom-left-radius:0;border:none;margin-right:10px}.form-div .filter-todos{margin-left:5px;outline:#88b8ff}.form-div .filter-todos select{background-color:#88b8ff}.clear-all{margin-top:30px;padding:6px 6px;border-radius:4px;border:none;font-family:"Montserrat",sans-serif;font-weight:700;background-color:#40465c;font-size:17px;color:#e6e6e6}.todo{margin-top:20px;width:50vw;display:grid;padding:6px 6px;background-color:#40465c;grid-template-columns:repeat(12, 1fr)}.todo .todo-item{padding:4px 4px;font-size:17px;font-weight:500;grid-column:1/10;background-color:#40465c}.todo .buttonDiv{grid-column:10/-1;width:100%;border:none}.todo .buttonDiv .completed{background-color:rgba(116,212,116,0.781);border:none;width:20px;height:20px}.todo .buttonDiv .delete{background-color:#f76b6b;border:none;width:20px;height:20px}.complete{opacity:0.3}.complete .todo-item{text-decoration:line-through}.complete .buttonDiv{display:none}@media only screen and (max-width: 600px){.completed,.delete{width:20px;height:20px}}

